import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
import MescrollMoreItemMixin from "@/components/mescroll-uni/mixins/mescroll-more-item.js";

export default {
	mixins: [MescrollMixin,MescrollMoreItemMixin], 
	props:{
		item: Object,
		index: Number,
		btn:Array,
		keys:Number,
		color:String,
		isclick:Boolean
	},
	data() {
		return {
			index1:0,
			itemData:[],
			cancel:'background:#cccccc',
			upOption:{
				use:false
			},
			downOption:{
				auto:false,
			},
		}
	},
	onReady() {
		console.log("要传递的值");
		console.log(this.item);
		console.log(this.key);
		this.itemData.push(this.item);
	},
	methods:{
		
		
		//跳转到详情页
		goDetail: function(item) {
			let detail = {
				Distance: item.Distance,
				budget: item.budget,
				code: item.code,
				content: item.content,
				delete: item.delete,
				destination: item.destination,
				door_time: item.door_time,
				duration: item.duration,
				label: item.label,
				longitude: item.longitude,
				name: item.name,
				origin: item.origin,
				send: item.send,
				state: item.state,
				tel: item.tel,
				type: item.type,
				uid: item.uid
			};
			if(this.isclick){
				uni.navigateTo({
					url: '../../pages/order-content/order-content?detailDate=' + encodeURIComponent(JSON.stringify(detail))
				});
			}
			// uni.navigateTo({
			// 	url: '../list2detail-detail/list2detail-detail?detailDate=' + encodeURIComponent(JSON.stringify(detail))
			// });
		},
		openpage(item){
			let server = {
				Distance: item.Distance,
				budget: item.budget,
				code: item.code,
				content: item.content,
				delete: item.delete,
				destination: item.destination,
				door_time: item.door_time,
				duration: item.duration,
				label: item.label,
				longitude: item.longitude,
				name: item.name,
				origin: item.origin,
				send: item.send,
				state: item.state,
				tel: item.tel,
				type: item.type,
				uid: item.uid
			};
				if(this.btn[1] =="立即上门"){
					uni.navigateTo({
						url: '../../pages/order-content/order-content?detailDate=' + encodeURIComponent(JSON.stringify(server))
					})
				}else{
					this.go()
				}
		},
		//删除订单
		deleteOrder(index){
			this.$emit("deleteOrder",index)
		},
		//拨打客服电话
		go(){
		 	uni.makePhoneCall({
		 	
		 	// 手机号
		    phoneNumber: '400-0015-021', 
		
			// 成功回调
			success: (res) => {
				console.log('调用成功!')	
			},
		
			// 失败回调
			fail: (res) => {
				console.log('调用失败!')
			}
			
		  });
		}
	}
}

